.nav__list {
  list-style-type: none;
  padding: 9px 0 0;
  display: flex;
  gap: 33px;
  margin: 0;
  flex-wrap: wrap;
  justify-content: flex-end;

  @include vp-1023 {
    padding: 0;
    gap: 23px;
  }

  @include vp-767 {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    gap: 26px;
  }
}

.nav a {
  color: $color-alice-blue;
  font-weight: 500;
  line-height: 20px;
  transition: opacity $trans-default;

  @include hover-focus {
    opacity: 0.8;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}
